.sr-only{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0, 0, 0, 0);border:0
}
.sr-only-focusable:active,.sr-only-focusable:focus{position:static;width:auto;height:auto;margin:0;overflow:visible;clip:auto
}
body>.container{margin-top:15px
}
.projectexhibition-section{margin-top:2%;padding:5%
}
.projectexhibition-section h4{color:#ffa500;font-weight:bold;font-size:30px
}
.projectexhibition-section .subheading{color:#3931af;font-size:20px
}
.projectexhibition-section .row{margin-top:10%
}
.how-img{text-align:center
}
.how-img img{width:90%;height:100%
}
.how-img .img-benchmark{width:100%
}
.how-img .img-vis{width:75%
}
.main-head{height:150px;background:#FFF
}
.sidenav{height:100%;background-color:#000;overflow-x:hidden;padding-top:15px;overflow-y:hidden
}
.main{padding:0px 10px
}
@media screen and (max-height: 450px){
.sidenav{padding-top:15px
}
}
@media screen and (max-width: 450px){
.login-form{margin-top:10%
}
.register-form{margin-top:10%
}
}
@media screen and (min-width: 768px){
.main{margin-left:25%
}
.sidenav{width:25%;position:fixed;z-index:1;top:0;left:0
}
}
.login-main-text{margin-top:5%;padding:60px;color:#fff
}
.login-main-text h2{font-weight:300
}
.btn-black{background-color:#000 !important;color:#fff
}
ul.social-network{list-style:none;display:inline;margin-left:0 !important;padding:0
}
ul.social-network li{display:inline;margin:0 5px
}
.social-network a.icoGitbub:hover{background-color:#F56505
}
.social-network a.icoFacebook:hover{background-color:#3B5998
}
.social-network a.icoTwitter:hover{background-color:#33ccff
}
.social-network a.icoGoogle:hover{background-color:#BD3518
}
.social-network a.icoDiary:hover{background-color:#0590B8
}
.social-network a.icoLinkedin:hover{background-color:#007bb7
}
.social-network a.icoGitbub:hover i,.social-network a.icoFacebook:hover i,.social-network a.icoTwitter:hover i,.social-network a.icoGoogle:hover i,.social-network a.icoDiary:hover i,.social-network a.icoLinkedin:hover i{color:#fff
}
a.socialIcon:hover,.socialHoverClass{color:#44BCDD
}
.social-circle li a{display:inline-block;position:relative;margin:0 auto 0 auto;border-radius:50%;text-align:center;width:50px;height:50px;font-size:20px
}
.social-circle li i{margin:0;line-height:50px;text-align:center
}
.social-circle li a:hover i,.triggeredHover{-moz-transform:rotate(360deg);-webkit-transform:rotate(360deg);-ms--transform:rotate(360deg);transform:rotate(360deg);-webkit-transition:all 0.2s;transition:all 0.2s
}
.social-circle i{color:#fff;-webkit-transition:all 0.8s;transition:all 0.8s
}
.Evaluation-section .img-eva{width:150px;height:150px
}
.Evaluation-section .box{width:100%;background-color:white;text-align:center;font-family:'Chilanka', cursive;border-radius:5px;padding:20px
}
.Evaluation-section .box:hover{-webkit-box-shadow:10px 10px 5px #aaaaaa;box-shadow:10px 10px 5px #aaaaaa
}
.Evaluation-section .box img{border-radius:50%;-webkit-box-shadow:10px 10px 5px #aaaaaa;box-shadow:10px 10px 5px #aaaaaa
}
.Evaluation-section .box h3{margin-top:10px
}
.Evaluation-section .box p{text-align:justify
}
